import React, {useState} from 'react';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Helmet } from 'react-helmet';
import { useForm } from 'react-hook-form';
import PropTypes from 'prop-types';
import qs from 'qs';
import { useLocation } from 'react-router-dom';
// component
import CountryNumber from './CountryNumber';
// scss
import '../scss/request/Request.scss';


const reqData=yup.object({
    email: 
        yup.string()
            .email('example@gmail.com  ')
            .required(' .'),
    phoneNumber: 
        yup.string(' ')
            .required(' ')
            .matches(/^[0-9]+$/, ' ')
            .min(9, "  . 9  .")
            .max(12,'  .')
            ,
    reqCategory1: 
        yup.string()
            .notOneOf([''], ' ')
            .required(' .'),
    reqCategory2:
        yup.string()
            .notOneOf([''], ' ')
            .required(' '),
    reqTitle: 
        yup.string()
            .required(' .'),
    reqContent: 
        yup.string()
            .required(' .'),
    reqAgree:
        yup.boolean()
            .oneOf([true], '  '),
})

const category=[
    {
        first: "",
        second: [""],
    }, {
        first: '',
        second: [
            '','', 'iOS', '()'
        ]
    }, {
        first: '',
        second: [
            '','', 'iOS', 'Windows', '()'
        ]
    }
]

function Request() {
    const query=qs.parse(useLocation().search,{ignoreQueryPrefix:true});
    const { register, handleSubmit, formState:{errors} }=useForm({
        resolver: yupResolver(reqData)
    });
    const onSubmit=()=> {
        window.location.href=`clean?${qs.stringify(query)}`;
    };

    const [select1, setSelect1] = useState('');
    const onSelect = (event) => {
        setSelect1(event.target.value);
    }
    const [file,setFile]=useState('');
    const onFile=(event)=>{
        setFile(event.target.value);
    }
    return (
        <form onSubmit={handleSubmit(onSubmit)} className='formBox' >
            <Helmet>
                <title>  | kakao </title>
            </Helmet>
            <div className='classify'></div>
            <div className='essential'>* </div>
            <div className='dataBox'>
                <div className='dataTitle'> *</div>
                <div className='dataInputBox oneInput'>
                    <input 
                        {...register('email')} 
                        type="text" 
                        name="email" 
                        id="" 
                        placeholder='example@kakao.com' 
                    />
                    <div className='error'>
                        {errors.email?.message}
                    </div>
                </div>
            </div>
            <div className='dataBox'>
                <div className='dataTitle'> *</div>
                <div className='dataInputBox numberInput'>
                    <CountryNumber /> 
                    <div className='inlineBlock numberInputBox'>
                        <input 
                            {...register('phoneNumber')}
                            type="text" 
                            name="phoneNumber" 
                            id="" 
                            placeholder='01012345678'
                        />
                    </div>
                    <div className='error'>
                        {errors.phoneNumber?.message}
                    </div>
                </div>
            </div>
            <div className='dataBox'>
                <div className='dataTitle'> *</div>
                <div className='dataInputBox categoryInput'>
                    <div className='categoryBox inlineBlock'>
                        <select
                            {...register('reqCategory1')} 
                            onChange={onSelect}
                            name="reqCategory1"
                        >
                            {category.map(data => (
                                <option key={data.first} value={data.first}>
                                    {data.first}
                                </option>
                            ))}
                        </select>
                        <div className='error'>
                            {errors.reqCategory1?.message}
                        </div>
                    </div>
                    <div className=' categoryBox inlineBlock'>
                        <select 
                            {...register('reqCategory2')} 
                            name="reqCategory2"
                        >
                            {select1 &&
                                category
                                    .filter(data => data.first === select1)[0].second
                                    .map(data => (
                                        <option 
                                            key={data} 
                                            value={data}
                                            // { index === 0 && selected } 
                                        >
                                            {data}
                                        </option>
                            ))}
                        </select>
                        <div className='error'>
                            {errors.reqCategory2?.message}
                        </div>
                    </div>
                </div>
            </div>
            <div className='dataBox'>
                <div className='dataTitle'> *</div>
                <div className='dataInputBox oneInput'>
                    <input 
                        {...register('reqTitle')}
                        type="text" 
                        name="reqTitle" 
                        id="" 
                        placeholder='  (20 )'
                    />
                    <div className='error'>
                        {errors.reqTitle?.message}
                    </div>
                </div>
            </div>
            <div className='dataBox'>
                <div className='dataTitle'> *</div>
                <div className='dataInputBox oneInput contentInput'>
                    <input 
                        className='contentInput'
                        {...register(
                            'reqContent',
                            {required:true},
                        )}
                        type="text" 
                        name="reqContent" 
                        id="" 
                    />
                    <div className={errors.reqContent?'error':'noErr'}>
                         
                    </div>
                </div>

            </div>
            <div className='dataBox'>
                <div className='dataTitle'> </div>
                <div className='fileInput'>
                    <label htmlFor="file"> </label>
                    <input type="file" name="file" id="file" onChange={onFile}/>
                    {file && <div className='fileName'>{file}</div>}
                    <div className='condition block'>  5, 30MB  .</div>
                </div>
            </div>
            <div className='dataPolicy'>
                <div>   </div>
                <div><strong>()    </strong></div>
                <div>
                    )          ,
                          .
                </div>
                <table>
                    <thead>
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td> ,  </td>
                            <td>    </td>
                            <td>3    </td>
                        </tr>
                    </tbody>
                </table>
                <div>    ,         .</div>
                <div>       .</div>
                <div>
                    <input 
                        {...register('reqAgree')}
                        type="checkbox" 
                        name="reqAgree"
                        id="reqAgree" 
                    />
                    <label htmlFor="reqAgree">
                          .
                    </label>
                    <span className='error'>
                        {errors.reqAgree?.message}
                    </span>
                </div>
            </div>
                <button 
                    type='submit'
                    className='reqButton'
                ></button>
        </form>
    );
}
Request.propTypes={
    classify: PropTypes.string.isRequired,
}

export default Request;