const SET_OS= 'osType/SET_OS';

export const setOS= os => ({
        type:SET_OS,
        os
    })

const initialState = 0;

export default (state = initialState, action)=>{
    switch (action.type){
        case SET_OS :
            return action.os; // 1. setOS os  . 2.   os .
        default:
            return state;
    }
}